@import "base/util"; // mixin



.header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	height: 60px;
	@include paddingset();
	// 중학교일 때
	.midSc &{
		margin-top: 27px;
	}
}
.chapterSet{
	display: flex;
}
.aidtChapter{
	display: inline-flex;
	align-items: center;
	color: var(--black);
	text-align: center;
	.landingTeacherHigh & {
		color: var(--white);
	}
	&__logo{
		font-size: 12px;
		width: 108px;
		height: 30px;
		line-height: 30px;
		border-radius: 7px;
		background-color: #efebeb;
		color: var(--black);
		margin-right: 10px;
	}
	&__school{
		display: flex;
		justify-content: center;
		align-items: center;
		font-size: 14px;
		width: 70px;
		height: 25px;
		border-radius: 12.5px;
		background-color: #7060ff;
		margin-right: 10px;
	}
	&__subject{
		font-size: 20px;
		letter-spacing: -1.8px;
	}
}
.infoSet{
	display: flex;
	align-items: center;
	&--info{
		color: var(--black);
		font-size: 18px;
		.landingTeacherHigh & {
			color: var(--white);
		}
	}
	&--help{
		background: var(--white);
		width: 42px;
		height: 42px;
		margin-left: 10px;
		background: url(/static/teacher/img/common/ico_help@2x.png) no-repeat center center;
		background-size: 24px 24px;
		border-radius: 12px;
		border: solid 1px #d1d1d1;
		background-color: var(--white);
	}
	&--home{
		background: var(--white);
		width: 42px;
		height: 42px;
		margin-left: 10px;
		background: url(/static/teacher/img/common/ico_home@2x.png) no-repeat center center;
		background-size: 24px 24px;
		border-radius: 12px;
		border: solid 1px #d1d1d1;
		background-color: var(--white);
	}
}


// 학급 선택 상자
.classSelect{
	position: relative;
	z-index: 1;
	ul,
	li{
		margin: 0;
		padding: 0;
	}
	>button{
		position: relative;
		color: var(--black);
		background-color: #b6c0ce;
		border: solid 2px var(--black);
		border-radius: 10px;
		width: 140px;
		height: 34px;
		margin-right: 50px;
		padding-right: 15px;
		&::after{
			position: absolute;
			right: 10px;
			font-size: 22px;
			@include font-ico-outline('\e5e1');
			transform: rotate(90deg);
		}
	}
	&__list{
		display: none;
		position: absolute;
		left: 0;
		top: 33px;
		width: calc(100% - 50px);
		border-radius: 10px;
		border: solid 2px var(--black);
		background-color: var(--white);
		padding-top: 10px;
		padding-bottom: 10px;
		a{
			display: block;
			text-align: center;
			line-height: 180%;
			&::before{
				content: '';
				margin-right:20px;
			}
			&::before{
				opacity: 0;
				font-size: 20px;
				@include font-ico-outline('\e5ca');
				margin-right: 10px;
			}
			&:hover,
			&:active,
			&:focus{
				background-color: var(--sky);
				&::before{
					@include font-ico-outline('\e5ca');
					opacity: 1;
				}
			}
		}
	}

	&:hover &__list,
	&:focus &__list,
	&:focus-within &__list{
		display: block;
	}
}


.navLeftTop{
	//z-index: 10;
	display: inline-block;
	vertical-align: middle;
	&--opener{
		border:1px solid var(--bd-color);
		width: 42px;
		height: 42px;
		vertical-align: middle; 
		margin-right: 20px;
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 10px;
		&::before{
			font-size: 26px;
			line-height: 1;
			@include font-ico-outline('\e5d2');
		}
	}
	&--opener[aria-expanded=false]
	&--opener[class=off] {
		& + .navLeftTopSet {display: none}
		& + .navLeftTopSet li{
			left: 0;
			top: 0;
			opacity: .2;
			transform: scale(.4);
		}
	}
	// 대화상자 닫기 버튼 (내부)
	&__list--opener{
		&:hover,
		&:focus,
		&:active{
			outline: 2px dotted var(--white);
			outline-offset: -4px;
		}
	}
	&__list--opener[aria-expanded=false] + ul li{
		left: 0;
		top: 0;
		visibility: hidden;
	}
	&__set{
		display: none;
		position: fixed;
		z-index: 1000;
		top: 0;
		left: 0;
		width: 100%;
		height: 100vh;
		&.on{
			display: block;
		}
		background-color: rgba(255, 255, 255, .9);
	}
	&__list{
		height: 100%;
		&--opener{
			position: absolute;
			left:0;
			top: 0;
			width: 460px;
			height: 460px;
			display: flex;
			justify-content: flex-end;
			align-items: flex-end;
			border-radius: 50%;
			color: var(--white);
			transform: translate(-50%, -50%);
			span{
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;
				width: 60%;
				height: 50%;
				font-size:16px;
				padding-top: 30px;
				text-align: center;
				background: url(/static/teacher/img/ico/ht_menu00@2x.png) no-repeat center 30px / 56px auto;
			}
			&::before{
				content: '';
				display: block;
				position: absolute;
				z-index: -1;
				left: 50%;
				top: 50%;
				width: 100%;
				height: 100%;
				border-radius: 50%;
				background-color: var(--black);
				transform: translate(-50%, -50%);
			}
		}
		>ul{
			position: absolute;
			left: 0;
			top: 0;
			margin: 0;
			padding: 0;
			max-width: 500px;
			max-height: 500px;
		}
		li{
			position: absolute;
			left: 0;
			top: 0;
			transition: all .2s ease;
			border-radius: 50%;
			opacity: 1;
			transform: scale(1);
			&:nth-child(1){transition-delay: 0.1s;left: 336px; top: 36px}
			&:nth-child(2){transition-delay: 0.2s;left: 299px; top: 150px}
			&:nth-child(3){transition-delay: 0.3s;left: 235px; top: 234px}
			&:nth-child(4){transition-delay: 0.4s;left: 150px; top: 300px}
			&:nth-child(5){transition-delay: 0.5s;left: 44px; top: 343px}
			&:nth-child(1) a{background:var(--black) url(/static/teacher/img/ico/ht_menu01@2x.png) no-repeat center center/ 28px auto}
			&:nth-child(2) a{background:var(--black) url(/static/teacher/img/ico/ht_menu02@2x.png) no-repeat center center/ 28px auto}
			&:nth-child(3) a{background:var(--black) url(/static/teacher/img/ico/ht_menu03@2x.png) no-repeat center center/ 28px auto}
			&:nth-child(4) a{background:var(--black) url(/static/teacher/img/ico/ht_menu04@2x.png) no-repeat center center/ 28px auto}
			&:nth-child(5) a{background:var(--black) url(/static/teacher/img/ico/ht_menu05@2x.png) no-repeat center center/ 28px auto}
			& a.on,
			&:hover a,
			&:focus a,
			&:focus-within a{
				background-color: var(--lipstick);
			}
		}
		a{
			display: block;
			width: 70px;
			height: 70px;
			border-radius: 50%;
			overflow: hidden;
			margin-top: -1px;
			box-shadow: 2px 2px 3px 0 rgba(0, 0, 0, 0.37);
			&:focus{									
				outline: 1px dotted var(--black);
				outline-offset: 3px;
			}
			span{
				display: none;
				white-space: nowrap;
				font-size: 16px;
			}
			&.on,
			&:hover,
			&:focus,
			&:active{
				span{
					display: block;
					position: absolute;
					left: 100%;
					bottom: 0;
					color: var(--lipstick);
				}
			}

		}
	}
	&.on &__list {
		&::before{
			content: '';
			display: block;
			position: fixed;
			z-index: -1;
			opacity: 0.5;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			//background-color: rgba(255,255,255,.95);
			background: #000 url(/static/teacher/img/tmp/hamburger.jpg) no-repeat 0 0;
		}
	}
	&--closer{
		@include flexCenter();
		position: absolute;
		z-index: 1010;
		left: 449px;
		top: 365px;
		width: 42px;
		height: 42px;
		box-shadow: 2px 2px 3px 0 rgba(0, 0, 0, 0.38);
		background-color: var(--black);
		border-radius: 50%;
		svg{
			fill: white;
			width: 24px;
			height: 24px;
			transform: rotate(45deg);
		}
	}
}


// 단원 선택 메뉴
.chapterList--opener[aria-expanded=true] + div{
	display: block;
}
.unitSelect {
	display: none;
	position: absolute;
	top:0;
	left: 100%;
	margin-left: 2px;
	color: var(--black);
	ul{
		width: 286px;
		margin: 2px 0 0;
		padding: 0;
		border-radius: 10px;  
		border: solid 2px var(--dark-grey);
		background-color:var(--white-two);
	}
	a {
		display: block;
		width: 100%;
		text-decoration: none;
		font-size: 16px;
		line-height: 37px;
		&[aria-expanded=true],
		&:hover,
		&:focus,
		&:active{
			background-color: var(--charcoal-grey) !important;
			color: var(--white);
		}
	}
	.dp1 a{
		padding-left: 25px;
		padding-right: 10px;
	}

	.chapterList {
		&__set {
			.chapterList__dp1 {
				div{
					display: none;
					position: absolute;
					top: 0;
					left: 100%;
					padding-left: 4px;
					margin-left: -2px;
				}
				
				/* [aria-expanded=false] + div{
					display: none;
				}
				[aria-expanded=true] + div{
					display: block;
				} */
				
				&:hover > .dp1,
				& > .dp1 > li:hover .chapterList__dp2,
				.dp2>li:hover .chapterList__dp3,
				&:focus-within > .dp1,
				& > .dp1 > li:focus-within .chapterList__dp2,
				.dp2>li:focus-within .chapterList__dp3 {
					display: block;
				}
			}
			.chapterList__dp1 > a,
			.chapterList__dp2 > a,
			.chapterList__dp3 > a {
				text-align: center;
				border-radius: 10px;
				border: solid 2px var(--dark-grey);
				background-color: var(--white-six);
				font-size: 16px;
				font-weight: 600;
			}
		}
	}
}