[data-theme=dark] {

	
}

$header-font-color : #f00;
$main-color : #f00;

// font color
.orange {
	color: var(--orange) !important;
}
.blue {
	color: var(--blue) !important;
}

.red {
	color: var(--red) !important;
}
.yellow{
	color: var(--sunshine-yellow) !important;
}
.c_complete{ color: var(--windows-blue); }  // 완료
.c_ing{ color: var(--bluegreen); }       // 진행중
.c_disable{ color: var(--warm-grey); }   // 판단불가

.bg_orange{ background-color: var(--dull-orange) !important; }
.bg_ravender{ background-color: var(--deep-lavender) !important; }

