:root,
[data-theme=light] {
	--body-landing-color: #fff; // font color
	--body-landing-bgcolor: #000; // background color
	--body-notice-bgcolor: 0, 0, 0; // notice bg
	--body-notice-opacity: 0.8; // notice opacity
	--wrapper-padding: 30px; // wrapper padding
	--landing-bg-color: #272f3a;
}

[data-theme=dark] {}

body{
	color: var(--body-landing-color);
	background-color: var(--landing-bg-color);
	overflow: hidden;
	position: relative;
	min-width: 1200px;
}
.wrap {
	position: relative;
	//z-index: -2;
	width: 100%;
	min-width: 1200px;
	max-width: 1920px;
	height: 100vh;
	margin: 0 auto;
}

.notice {
	position: fixed;
	left: 0;
	bottom: 0;
	width: 100%;
	text-align: right;
	background-color: rgba(var(--body-notice-bgcolor), var(--body-notice-opacity));
	color: var(--body-landing-color);
	padding: 10px 30px;
}

.header {
	position: relative;
	z-index: 100;
	@include paddingset();
}

.content {
	@include paddingset();

	main {
		display: flex;
		height: calc(100vh - 60px);
	}

	.greeting {
		width: 41%;
		position: relative;
	}

	.landingNavSet {
		width: 59%;
	}
}

.greetingMsg {
	position: absolute;
	top: 14%;
	left: 18%;
	width: 82%;
	padding: 30px 30px 35px 38px;
	color: #c8c8c8;
	border-radius: 30px;
  background-color: #1d2026;
	&::before {
		content: '';
		position: absolute;
		z-index: 1;
		top: 100%;
		left: -10.5%;
		width: 65%;
		height: 430px;
		background: url(/static/teacher/img/landing/high/h_parang.png) no-repeat center center;
	}

	&::after {
		content: '';
			position: absolute;
		bottom: 0;
		left: 10%;
		width: 30px;
		height: 20px;
		transform: translateY(100%);
		background: url(/static/teacher/img/landing/high/h_parang_arr.png) no-repeat center center;
	}

	h2 {
		margin-bottom: 24px;
		font-size: 32px;
		color: #fff;
	}

	p {
		line-height: 185%;
	}
}

.landingNav {
	position: relative;
	padding-top: 10%;
	padding-right: 22.2%;
	padding-left: 7.9%;

	&::before {
		content: '';
		position: absolute;
		z-index: -1;
		top: 0;
		left: 0;
		width: 100%;
		padding-bottom: 100%;
		}

	&__center {
		position: absolute;
		z-index: 1;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 253px;
		height: 253px;
		border-radius: 50%;
		box-shadow: 0 0 200px var(--black);
		background: url(/static/teacher/img/landing/high/nav_center.png) no-repeat center center;
		color: #000;
		display: flex;
		align-items: flex-end;
		justify-content: center;
		padding-bottom: 43px;
		font-weight: 800;
		span{
			display: inline-block;
			position: absolute;
			display: block;
			width: 0;
			cursor: pointer;
			border-left: 12px solid transparent;
			border-right: 12px solid transparent;
			border-bottom: 23px solid #000;
			transition: all 0.5s ease-out;
			&.arrow1{
				left: -16%;
				top: 55%;
				transform: rotate(-100deg);
			}
			&.arrow2{
				top: -14%;
				left: 43%;
				transform: rotate(-3deg);
			}
			&.arrow3{
				bottom: 15%;
				right: -6%;
				transform: rotate(124deg);
			}
			&.on{
				border-bottom-color: #7668F0;
			}
		}
	}

	&__list {
		position: absolute;
		border: 1px solid rgba(255, 255, 255, 0.1);
		width: 100%;
		height: 100%;
		border-radius: 50%;

		&::before {
			content: '';
			border: 1px solid rgba(255, 255, 255, 0.1);
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			border-radius: 50%;
			transform: scale(2.28);
		}

		>li {
			position: absolute;
		
			/* &::before {
				content: '';
				position: absolute;
				width: 0;
				border-left: 12px solid transparent;
				border-right: 12px solid transparent;
				border-bottom: 23px solid #000;
				transition: all 0.5s ease-out;
			} */

			&:focus,
			&:hover,
			&:focus-within {
				&::before {
					border-bottom-color: #f50;
				}
			}

			&:nth-child(1) {
				top: 46%;
				left: -12%;
				&::before {
					left: 161%;
					top: 22%;
					transform: rotate(-100deg);
				}
			}

			&:nth-child(2) {
				top: -10%;
				left: 35%;

				&::before {
					top: 156%;
					left: 50%;
					transform: rotate(-3deg);
				}
			}

			&:nth-child(3) {
				bottom: 14.4%;
				right: -5%;

				&::before {
					top: -8%;
					left: -58%;
					transform: rotate(124deg);
				}
			}
			&>a:hover + ul,
			&>a:focus + ul,
			&:focus-within ul,
			&.on>ul {
				display: block;
			}

			>ul {
				position: absolute;
				top: 28%;
				left: 99%;
				width: 210px;
				padding-left: 40px;
				display: none;
				&:hover,
				&:focus-within {
					display: block;
				}

				>li {
					margin-bottom: 5px;

					>a {
						display: block;
						padding: 10px 22px;
						color: #fff;
						background-color: var(--black-two);
						border-radius: 30px;

						strong {
							color: #4bc070;
						}
					}
				}
			}
		}

		>li>a {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 190px;
			height: 190px;
			border-radius: 50%;
			border: solid 1px rgba(255, 255, 255, 0.1);
			//background-color: #29313e;
			font-size: 26px;
			font-weight: 700;
			color: #fff;
			transition: all 0.5s ease-out;
			background-color: var(--landing-bg-color);

			&::before {
				content: '';
				width: 60px;
				height: 60px;
				margin-top: 38px;
				margin-bottom: 18px;
				background: url(/static/teacher/img/landing/high/nav_01_on.png) no-repeat center center;
				background-size: contain;
			}

			&.menu1::before {
				background-image: url(/static/teacher/img/landing/high/nav_01_off.png);
			}

			&.menu2::before {
				background-image: url(/static/teacher/img/landing/high/nav_02_off.png);
			}

			&.menu3::before {
				background-image: url(/static/teacher/img/landing/high/nav_03_off.png);
			}

			&:hover,
			&:focus,
			&.on {
				transform: scale(1.2);

				&.menu1::before {
					background-image: url(/static/teacher/img/landing/high/nav_01_on.png);
				}

				&.menu2::before {
					background-image: url(/static/teacher/img/landing/high/nav_02_on.png);
				}

				&.menu3::before {
					background-image: url(/static/teacher/img/landing/high/nav_03_on.png);
				}

				background-image: radial-gradient( farthest-corner at 40px 40px, #8679EB 0%, #5f4fd5 50%, #3728bc 100% );
			}
		}
	}

	nav {
		position: relative;
			border-radius: 50%;
		padding-bottom: 100%;
	}
}

.decorate {
	span {
		position: absolute;
		z-index: 1;
		font-size: 60px;
		font-weight: 100;
	}

	&__circle {
		left: -2%;
		top: 7%;
	}

	&__box {
		left: 30%;
		bottom: 14%;

	}

	&__box2 {
		left: -1%;
		bottom: -12%;

	}

	&__corn {
		left: 47%;
		top: 17%;
	}

	&__horn {
		right: 20.7%;
		bottom: 4%;
	}

	&__tran {
		right: 3.7%;
		top: 13.8%;
	}

	&__dot {
		right: 8%;
		top: 40%;
	}

	&__plus {
		right: 10%;
		top: 6%;
		color: #383f49;
	}

	&__plus2 {
		font-weight: 300;
		left: 37.5%;
		top: 45%;
		color: #50565e;
	}

	&__plus3 {
		font-weight: 600;
		left: 2.8%;
		bottom: 19%;
		color: #434187;
	}
}